* {
    outline: none;
    word-wrap: break-word;
    word-break: break-all;
    box-sizing: border-box;
}

html{
    background: $g-body-bg;
}

body {
    padding: 0;
    margin: 0;
    color: $g-color;
    word-break: break-all;
    word-wrap: break-word;
    box-sizing: border-box;
    font-family: $g-font-family;
    font-size: $g-font-size;
    line-height: $g-line-height;
}

ul, li, p, dd, dl, dt, h1, h2, h3, h4, h5 {
    padding: 0;
    margin: 0;
    list-style: none;
}

div:after, p:after, ul:after, li:after, ol:after, dl:after, dd:after, dt:after,
h1:after, h2:after, h3:after, h4:after, h5:after {
    clear: both;
    content: " ";
    display: block;
}

dd, dt {
    font-weight: normal;
}

img {
    border: 0;
    object-fit: cover;
}

a {
    &,
    &:link,
    &:active,
    &:visited {
        color: $g-link-color;
        text-decoration: none;
        cursor: pointer;
    }

    &:hover {
        text-decoration: none;
        color: $g-link-hover-color;
    }
}

input[type=text], textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=number] {
    -webkit-appearance: textfield;
}

button {
    cursor: pointer;
}

.radio, .checkbox {
    display: inline-block;
    vertical-align: middle;
    line-height: 1;
}

*:focus {
    box-shadow: none;
}

.iconfont {
    vertical-align: -1px;
}

.btn-default {
    color: #333;
    background-color: #fff;
    border-color: #ccc;

    &:focus,
    &.focus {
        color: #333;
        background-color: #e6e6e6;
        border-color: #8c8c8c;
    }

    &:hover {
        color: #333;
        background-color: #e6e6e6;
        border-color: #adadad;
    }

    .badge {
        color: #fff;
        background-color: #333;
    }
}

.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    color: #333;
    background-color: #e6e6e6;
    border-color: #adadad;
}

.btn-default:active:hover,
.btn-default.active:hover,
.open > .dropdown-toggle.btn-default:hover,
.btn-default:active:focus,
.btn-default.active:focus,
.open > .dropdown-toggle.btn-default:focus,
.btn-default:active.focus,
.btn-default.active.focus,
.open > .dropdown-toggle.btn-default.focus {
    color: #333;
    background-color: #d4d4d4;
    border-color: #8c8c8c;
}

.btn-default:active,
.btn-default.active,
.open > .dropdown-toggle.btn-default {
    background-image: none;
}

.btn-default.disabled:hover,
.btn-default[disabled]:hover,
fieldset[disabled] .btn-default:hover,
.btn-default.disabled:focus,
.btn-default[disabled]:focus,
fieldset[disabled] .btn-default:focus,
.btn-default.disabled.focus,
.btn-default[disabled].focus,
fieldset[disabled] .btn-default.focus {
    background-color: #fff;
    border-color: #ccc;
}

.form-control {
    border-radius: 2px;
    font-size: 12px;
    height: auto;
}

.form-control:focus {
    box-shadow: none;
}

.alert {
    padding: 10px 15px;
}

select.form-control:not([size]):not([multiple]) {
    height: auto;
}

.breadcrumb {
    padding: 10px 15px;

    .breadcrumb-item {
        color: #666;
        font-size: 12px;
    }

    .active {
        color: #000;
    }
}

.page-link {
    padding: 5px 10px;

    &:focus {
        box-shadow: none;
    }
}

.page-item {
    font-size: 12px;

    &.active {
        .page-link {
            background-color: #0098CE;
            border-color: #0098CE;
        }
    }
}

/*==jQuery UI==*/
.ui-sortable-placeholder {
    border: 2px #0099CC dashed !important;
    visibility: visible !important;
    display: block !important;
    background: none !important;
}

.bg-cover {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("data:image/png;base64,");
}

.swiper{
    height: 100% !important;

    .swiper-slide-image {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
